.region {
  &.cover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;

    .bg-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url('../../images/background/1.jpg');
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 1;
      animation: fadeIn 1s linear;
    }
    .default-bg {
      position: relative;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url('../../images/background/1.jpg');
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 0;
    }
  }
}